*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;

    font-family: 'Cardo', serif;    
    	/* Cursor styles */
	--cursor-stroke: #575655;
	--cursor-fill: none; 
	--cursor-stroke-width: 2px;
    cursor: none;

}

@font-face {
    font-family: "KENOKY-Black";
    src: url("./fonts/KENOKY-Black.woff2") format('woff');
}

@font-face {
    font-family: "KENOKY-SeimiBold";
    src: url("./fonts/KENOKY-SemiBold.woff2") format('woff');
}

html {

    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 10px;
}
