.cursor {
	display: none;
}

@media (any-pointer: fine) {
	.cursor {
		position: fixed;
		top: 0;
		left: 0;
		display: block;
		pointer-events: none;
		opacity: 0;
	}
	.cursor__inner {
		fill: var(--cursor-fill);
		stroke: var(--cursor-stroke);
		stroke-width: var(--cursor-stroke-width);
	}
}
.content__back {

    position: absolute;
    top: 3.3%;
    left: 3.3%;
    margin: auto;
    cursor: pointer;
    background-color:transparent;
    border-style: none;
}



